@media all and (max-width: 1000px)
  .header__logo
    height: 30px

  .header__profile
    height: 20px

  .header__nav a
    text-decoration: none
    color: #fff
    font-size: 1em
    font-weight: 400

  .banner__content
    position: absolute
    top: 45%
    transform: translateY(-50%)
    left: 50px
    width: 60%
    color: #392683

  .banner__title
    font-size: 1.8em
    line-height: 30px

  .banner__description
    margin-top: 15px
    margin-bottom: 15px
    font-size: 0.8em

  .banner__link
    font-size: 0.8em
    padding: 5px
    color: #392683
    display: inline
    width: fit-content
    margin: 0 auto

  .topproducts
    margin: 0
    margin-top: 15px
    padding: 15px

  .topproducts__title
    font-size: 1.5em

  .topproducts__media
    margin-top: 25%
    position: relative
    align-items: baseline
    justify-content: center

  .media__container
    width: 30%
    padding: 0
    display: flex
    justify-content: center

  .media__middle
    display: none

  .media__img
    opacity: 0.6
    display: block
    height: auto
    transition: .5s ease
    backface-visibility: hidden
    padding: 0
    filter: drop-shadow(10px 6px 8px #afafaf81)

  #media__top__img
    position: absolute
    justify-self: center
    right: 0
    left: -25%
    bottom: 0
    width: 150%
    padding: 0
    opacity: 1

  .topproducts__link
    border-style: none
    text-decoration: underline
    font-size: 1em

  .newsletter, .complains
    width: 60%

  .complains__title
    width: 100%
    padding-right: 0

  footer
    width: 100%
    flex-direction: column
    align-items: center
    padding: 10px

  .footer__logo
    padding-top: 15px
    padding-bottom: 15px

  .footer__content
    width: 100%
    align-items: flex-start
    justify-content: center
    font-size: 0.5em

  .footer__contact
    width: 35%

  .aboutus__content
    width: 50%

@media all and (max-width: 500px)
  header
    padding: 5px 15px 5px 15px

  .header__logo
    height: 20px

  .header__profile
    height: 15px

  .header__nav a
    text-decoration: none
    color: #fff
    font-size: 0.6em
    font-weight: 400

  .banner
    display: flex
    flex-direction: column

  .banner__img
    position: relative

  .banner__content
    width: 100%
    left: 0%
    padding: 10px
    color: #392683
    margin-top: 180px

  .banner__dots
    width: 10%
    position: absolute
    bottom: 10px
    margin-left: auto
    margin-right: auto
    left: 0
    right: 0

  .banner__title
    font-size: 1.5em
    line-height: 30px

  .banner__description
    margin: 10px
    font-size: 0.8em

  .banner__link
    padding: 5px
    text-decoration: none
    color: #392683
    border-style: solid
    justify-self: center
    display: block
    width: fit-content
    margin: 0 auto

  .topproducts
    margin-top: 70%

  .footer__content
    flex-direction: column
    align-items: center

  .footer__contact
    width: 70%

  .contact__content
    align-content: space-around

  .footer__aboutus
    width: 70%

  .aboutus__content
    width: 100%